<template>
  <div class="city-menu">
    <div class="container">
      <div class="row">
        <div class="col-md-4">
          <div class="mb-3">
            <label for="cityName" class="form-label"></label>
            <select
              class="form-select"
              id="cityName"
              aria-label=""
              v-model="searchCity"
            >
              <option
                v-for="c in chooseCity"
                :key="c.cityEng"
                :value="c.cityEng"
                >{{ c.city }}</option
              >
            </select>
          </div>
        </div>
        <div class="col-md-8">
          <div class="mb-3">
            <label for="bikeRoute" class="form-label"></label>
            <select
              class="form-select"
              id="bikeRoute"
              aria-label=""
              v-model="searchCityRoute"
            >
              <option
                v-for="(r, index) in chooseRoute"
                :key="index"
                :value="index"
                >{{ index }} - {{ r.RouteName }}</option
              >
            </select>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import store from "../store";

export default {
  name: "cityMenu",
  data() {
    return {
      searchCity: "",
      searchCityRoute: "",
      cityName: [
        { city: "", cityEng: "Taichung" },
        { city: "", cityEng: "HsinchuCounty" },
        { city: "", cityEng: "Keelung" },
        { city: "", cityEng: "MiaoliCounty" },
        { city: "", cityEng: "ChanghuaCounty" },
        { city: "", cityEng: "NewTaipei" },
        { city: "", cityEng: "NantouCounty" },
        { city: "", cityEng: "YunlinCounty" },
        { city: "", cityEng: "ChiayiCounty" },
        { city: "", cityEng: "Chiayi" },
        { city: "", cityEng: "PingtungCounty" },
        { city: "", cityEng: "YilanCounty" },
        { city: "", cityEng: "HualienCounty" },
        { city: "", cityEng: "TaitungCounty" },
        { city: "", cityEng: "KinmenCounty" },
        { city: "", cityEng: "PenghuCounty" },
        { city: "", cityEng: "Taoyuan" },
        { city: "", cityEng: "Taipei" },
        { city: "", cityEng: "Kaohsiung" },
        { city: "", cityEng: "Tainan" }
      ]
    };
  },
  computed: {
    chooseCity: {
      get() {
        return this.$data.cityName;
      },
      set(val) {
        store.commit("SET_CITY", val);
      }
    },
    chooseRoute: {
      get() {
        return store.state.bikeRoute;
      },
      set(val) {
        //index
        store.commit("SET_BIKEROUTE_TARGETINDEX", val);
      }
    }
  },
  watch: {
    //
    searchCity(val) {
      store.dispatch("READ_BIKEROUTE_DATA", val);
    },
    searchCityRoute(val) {
      store.dispatch("READ_BIKETARGET_DATA", val);
      this.$emit("display");
    }
  },
  methods: {}
};
</script>
